.slider-arrow {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.arrow-btn {
  top: 35%;
  border-radius: 20px;
  padding: 40px 5px;
  height: 100px;
  border: lightgrey 2px solid;
  position: relative;
  text-align: center;
  pointer-events: all;
}
.next {
  float: right;
}
.button__bar {
    display: inline-block;
    vertical-align: middle;
    margin: auto;
    padding: 0;
    /* background: rgba(255, 255, 255, 0.3); */
    border-radius: 40px;
    width: 100%;
    text-align: center;
    position: absolute;
    top: 0;
  }
  
  .button__bar li {
    list-style: none;
    cursor: pointer;
    display: inline-block;
    margin: 0 3px;
    padding: 0;
  }
  
  .button__bar li button {
    border: none;
    background: #C4C4C4;
    color: transparent;
    cursor: pointer;
    display: block;
    height: 15px;
    width: 15px;
    border-radius: 7.5px;
  }
  
  .button__bar li.slick-active button {
    background-color: #F8812A;
  }
  
  .button__bar li button:hover,
  .button__bar li button:focus {
    /* background: rgba(247, 34, 34, 0.8); */
    outline: 0;
  }
  
  .slick-slide img {
    border: 5px solid #fff;
    display: block;
    margin: auto;
  }
  
  .paragraph-col div{
      width: 100%;
  }
  .ticket-status-info-col{
      margin: 0px 50px;
  }
  @media only screen and (min-width: 720px) {
    .ticket-status-info-col {
        width: 70%;
    }
  }